/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { reduxForm, Field } from 'redux-form';
import InputAdornment from '@material-ui/core/InputAdornment';
import Email from '@material-ui/icons/Email';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
import { 
  setUsernameActionCreator, 
  setPasswordActionCreator,
  setPasswordVisibilityActionCreator,
  handleRegisterActionCreator 
} from '../../reducers/auth/authActionCreators';
import styles from './RegisterPageStyles';
/* eslint-enable */

export const PAGE_NAME = 'Sign up';

let ContentSeagment = ({
  classes,
  authInfo,
  setUsername,
  setPassword,
  togglePasswordVisibility
}) => (
<DialogContent>
    <DialogContentText>
      to continue with ReBind.
    </DialogContentText>
    <br/>

      <Field name="email" component={() => (
        <TextField
        fullWidth
        color="inherit"
        type="email"
        label="Email"
        placeholder="Enter your Email"
        className={classes.textField}
        value={authInfo.username}
        onChange={event => setUsername(event.target.value)}
        InputProps={{
          endAdornment: (
            <InputAdornment position="start">
              <Email />
            </InputAdornment>
          )
        }}
        />)
        }
      />

    <br/>
    <Field name="password" component={ () => (
      <TextField
        fullWidth
        color="inherit"
        type={authInfo.isPasswordVisible ? 'text' : 'password'}
        label="Password"
        placeholder="Enter your Password"
        className={classes.textField}
        value={authInfo.password}
        onChange={event => setPassword(event.target.value)}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                aria-label="Toggle password visibility"
                onClick={() => togglePasswordVisibility(!authInfo.isPasswordVisible)}
              >
               {authInfo.isPasswordVisible ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          )
        }}
    />)
  }
  />

    <br/>
</DialogContent>
);

ContentSeagment.propTypes = {
  classes: PropTypes.object.isRequired,
  authInfo: PropTypes.object.isRequired,
  setUsername: PropTypes.func.isRequired,
  setPassword: PropTypes.func.isRequired,
  togglePasswordVisibility: PropTypes.func.isRequired
};

ContentSeagment = reduxForm({
  form: 'register'
})(ContentSeagment);
ContentSeagment = withStyles(styles)(ContentSeagment);
const mapStateToContentProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToContentProps = dispatch => ({
  setUsername: username => dispatch(setUsernameActionCreator(username)),
  setPassword: password => dispatch(setPasswordActionCreator(password)),
  togglePasswordVisibility: isVisible => dispatch(setPasswordVisibilityActionCreator(isVisible))
});
export const Content = connect(
  mapStateToContentProps,
  mapDispatchToContentProps
)(ContentSeagment);


const ButtonsSeagment = ({
  authInfo,
  classes,
  handleRegister
}) => (
  <DialogActions>
    <Button
      label="Submit"
      style={styles}
      variant="contained"
      color="primary"
      className={classes.button}
      onClick={() => handleRegister(authInfo)}
    >
      Register
    </Button>
  </DialogActions>
);

ButtonsSeagment.defaultProps = {
  authInfo: {
    username: '',
    password: ''
  }
};

ButtonsSeagment.propTypes = {
  classes: PropTypes.object.isRequired,
  authInfo: PropTypes.object,
  handleRegister: PropTypes.func.isRequired
};

const StyledButtons = withStyles(styles)(ButtonsSeagment);

const mapStateToButtonsProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToButtonsProps = dispatch => ({
  handleRegister: authInfo => dispatch(handleRegisterActionCreator(authInfo))
});

export const Buttons = connect(
  mapStateToButtonsProps,
  mapDispatchToButtonsProps
)(StyledButtons);

